<template>
  <div class="Field">
    <text class="Text__White Text__Size--16">{{label}}</text>
    <input :type="type" :placeholder="placeholder" class="Field__Input">
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      required: true,
    },

    type: {
      type: String,
      default: 'text',
    },

    placeholder: {
      type: String,
    },
  },
};
</script>

<style scoped lang="stylus">
  @import '~@/assets/style/field.styl'
</style>



